<template>
    <div>
        <a class="link-primary" @click="content!==null ? content = null : show(url, contentId)">
            <slot>Показать</slot>
        </a>
    </div>
    <Card 
        v-if="content!==null"
        :content="content" 
        :correspondenceOfFieldsAndRusLabels="correspondenceOfFieldsAndRusLabels"
    />
</template>

<style scoped>
    a {
        cursor: pointer;
    }
</style>

<script>
import Axios from '@/axios'
import Card from '@/components/basic/CharacteristicCard.vue'

export default {
    components: { Card },
    props:{
        url: String,
        contentId: Number,
        correspondenceOfFieldsAndRusLabels: Object
    },
    data(){
        return{
            content: null
        }
    },
    methods: {  
        async show(url, id){
            this.content = (await Axios.get(`${url}/${id}`)).data
        }
    }
}
</script>